import React from 'react';
import "./SearchBar.css";
import { Form, Input} from 'semantic-ui-react';
import axios from "axios";

export class SearchBar extends React.Component {
    state = {
        input:"",
        inputName:"Submit",
        labelName:"SearchBarLabel",
        results: [],
        currentIndex: 0,
        wordIndex: 0
    }

    onChange = async (e, {name, value}) => {
        
        const autocorrect = JSON.stringify({autoCorrect: value})
        axios.post("https://cyberminerkwic.herokuapp.com/autocorrect", autocorrect, 
            { headers: {
                'Content-Type': 'application/json'
            }}
        )
            .then(res => {
                const autoCorrect = res.data.autoCorrect;

                this.setState({[name]:value, results:autoCorrect});
            })
            .catch(error => {
                console.log(error);
            })
    }

    onSubmit = () => {
        if(this.state.input.length) {
            this.setState({
                inputName:"Submitted",
            labelName:"SearchedBarLabel"});
            this.props.onSubmit(this.state.input);
        }
    }

    correct = async (e) => {
        const value = e.target.value;
        const wordIndex = this.state.wordIndex;

        console.log(value);
        if(value[value.length - 1] === " ")
        {
            const currentIndex = value.length - 1;
            const word = value.substring(wordIndex, currentIndex);
            for(let i = 0, j = wordIndex; i < word.length; i++, j++) {
                value[JSON] = word[i];
            }

            this.setState({value: value, wordIndex: value.length});
        }
    }

    renderSearchBar = () => {
        return (
            <div className="Form">
                <div className="ui container">
                        <Form className={this.state.inputName} onSubmit={this.onSubmit}>
                            <Form.Field>
                                <label className={this.state.labelName}>CyberMiner</label>
                                <Input
                                    list="autocorrect"
                                    type="Input"
                                    name="input"
                                    placeholder="Search..."
                                    value={this.state.input}
                                    onChange={this.onChange}
                                    />
                                    <datalist id="autocorrect">
                                        <option 
                                            value={this.state.results}
                                            onClick={this.correct} />
                                    </datalist>
                            
                                                
                            </Form.Field>
                        </Form>
                </div>
            </div>
        );
    }

    render = () => {
        return (
        <div className="SearchBar">
            {this.renderSearchBar()}
        </div>);
    }
}